import React from 'react'
import Gallery from '../Components/Gallery.js';

import img1 from '../Images/I001.jpg';
import img2 from '../Images/I002.jpg';
import img3 from '../Images/I003.jpg';
import img4 from '../Images/I004.jpg';
import img5 from '../Images/I005.jpg';
import img6 from '../Images/I006.jpg';
import img7 from '../Images/I007.jpg';
import img8 from '../Images/I008.jpg';
import img9 from '../Images/I009.jpg';
import img10 from '../Images/I010.jpg';
import img11 from '../Images/I011.jpg';
import img12 from '../Images/I012.jpg';
import img13 from '../Images/I013.jpg';
import img14 from '../Images/I014.jpg';
import img15 from '../Images/I015.jpg';
import img16 from '../Images/I016.jpg';
import portraitimg from '../Images/wigga.png';
import { LazyLoadImage } from 'react-lazy-load-image-component';
import 'react-lazy-load-image-component/src/effects/opacity.css';



let urls = [img1, img2, img3, img4, img5, img6, img7, img8, img9, img10, img11, img12, img13, img14, img15, img16]



class Portraits extends React.Component {

    constructor(props) {
        super(props);
        this.state = {loaded: false};
      }



    componentDidUpdate() {
        if (this.state.loaded) {
            setTimeout(function(){
                var elementer = document.getElementById("grabthis-4");
                if (elementer == null) {
                    return
                }
                elementer.classList.add('intro-4')
                setTimeout(function(){
                    var elementer = document.getElementById("grabthis-5");
                    if (elementer == null) {
                        return
                    }
                    elementer.classList.add('intro-5')
                    setTimeout(function(){
                        var elementer2 = document.getElementById("grabthis-6");
                        if (elementer2 == null) {
                            return
                        }
                        elementer2.classList.add('intro-3')
                    }, 300); 
        
                }, 200); 
            }, 1200); 
        } 
    }


    setLoaded = () => {
        this.setState({loaded: true});
    }



    

    render() {
    return (
        <div className='page'>
        <div className = 'portraitcontainer'  >
            <h1 id = 'grabthis-6' className = 'portraits-text'>PORTRAITS </h1>
            <LazyLoadImage src={portraitimg} id='portraitimg'  alt="girl standing in field"effect="opacity" delayMethod= "debounce" delayTime = '2000' placeholder={<span className = 'bitch'></span>} afterLoad={() => this.setLoaded(true)} />
            <div className='grey-portait-box' id = 'grabthis-4'></div>
            <div className='brown-portrait-box' id = 'grabthis-5'></div>
            <div></div>
        </div>
         <Gallery  imageUrls={urls} />
        </div>
    )
    }
}

export default Portraits;