import React, { Component } from "react";
import emailjs from 'emailjs-com';


class Form extends Component {
  constructor() {
    super();

    this.state = {
      name: "",
      email: "",
      message: "",
      number: "", 
      package: "",
      status: "SUBMIT"
    };   
  }

  handleChange(event) {
    const field = event.target.id;
    if (field === "name") {
      this.setState({ name: event.target.value });
    } else if (field === "email") {
      this.setState({ email: event.target.value });
    } else if (field === "message") {
      this.setState({ message: event.target.value });
    } else if (field === "number") {
      this.setState({ number: event.target.value });
    }  else if (field === "number") {
        this.setState({ number: event.target.value });
    }
  }

  handleSubmit(event) {
    event.preventDefault();  

    const templateParams = {
      name: this.state.name,
      email: this.state.email,
      message: this.state.message,
      number: this.state.number,
    };
    this.setState({ status: "Sending" });  
    emailjs.send('service_8vy6avb', 'template_q1i3rdi', templateParams, 'user_GbWtsCTWKsdyL2mOdz1tH')
    .then((response) => {
        this.setState({ name: "", email: "", message: "", number: "", status: "Sent" });
    }, (err) => {
      this.setState({status: "Failed, Try Again" });
    });
  }

  render() {
    let buttonText = this.state.status;
    
    return (      
        <form onSubmit={this.handleSubmit.bind(this)} method="POST" className='format'>
            <div>
                <input
                type="text"
                id="name"
                className='whitebox'
                placeholder = 'Name'
                value={this.state.name}
                onChange={this.handleChange.bind(this)}
                required
                
                />
            </div>
            <div>
                <input
                type="email"
                id="email"
                placeholder = 'Email'
                className='whitebox'
                value={this.state.email}
                onChange={this.handleChange.bind(this)}
                required
                />
            </div>
            <div id='contactrow'>
                <input
                type="tel"
                id="number"
                className='whitebox'
                placeholder = 'Number'

                value={this.state.number}
                onChange={this.handleChange.bind(this)}
                required
                />
                <select
                className='whitebox'
                id="package"
                name = 'package'
                >
                    <option value="Just Inquring">Choose a package</option>
                    <option value="Standard Package" >Standard Package</option>
                    <option value="Premium Package">Premium Package</option>
                    <option value="Just Inquring">Just Inquring</option>
                </select>
            </div>
            <div>
                <input
                id="message" 
                className='whitebox'
                placeholder = 'Any additional information'
                value={this.state.message}
                onChange={this.handleChange.bind(this)}
                />
            </div>
            <button  className = 'button' type="submit" id='submit'>{buttonText}</button>
    </form>   
    );
}
}

export default Form;